function switchTheme(e) {
    if (e.target instanceof HTMLInputElement) {
        var themes_1 = { green: 'theme-green', orange: 'theme-orange' };
        console.log("[" + themes_1.green + "]");
        if (e.target.checked) {
            document.querySelectorAll("." + themes_1.green).forEach(function (el) {
                el.classList.remove(themes_1.green);
                el.classList.add(themes_1.orange);
            });
        }
        else {
            document.querySelectorAll("." + themes_1.orange).forEach(function (el) {
                el.classList.remove(themes_1.orange);
                el.classList.add(themes_1.green);
            });
        }
    }
}
