var categoryCards = document.querySelectorAll('.category__card');
var isFlipped = false;
function flip(e) {
    if (e.target instanceof HTMLElement &&
        e.currentTarget instanceof HTMLElement &&
        e.target.classList.contains('bi-arrow-repeat')) {
        console.log(e.currentTarget);
        e.currentTarget.classList.add('flipped');
        var cardContent = e.currentTarget.querySelector('.category__card-content');
        cardContent.addEventListener('transitionend', function () {
            isFlipped = true;
        });
    }
}
function flipBack(e) {
    if (e.currentTarget instanceof HTMLElement && e.currentTarget.classList.contains('flipped') && isFlipped) {
        e.currentTarget.classList.remove('flipped');
        isFlipped = false;
    }
}
